.section-4 {
  background-color: $section-color;

  .wrapper {
    @media (min-width: 1440px) {
      padding-top: 65px;
      padding-bottom: 92px;
    }
  }

  .section-title {
    margin-bottom: 20px;

    @media (min-width: 768px) {
      margin-bottom: 56px;
    }
  }

  .section-4-row {
    @media (min-width: 992px) {
      margin-left: -70px;
      margin-right: -70px;
    }
  }
}

.col-subscribition {
  margin-bottom: 20px;

  @media (min-width: 768px) {
    margin-bottom: 0;
  }

  @media (min-width: 992px) {
    padding-right: 70px;
    padding-left: 70px;
  }
}

.col-follow-us {
  
  @media (min-width: 992px) {
    padding-right: 70px;
    padding-left: 70px;
  }

  // display: flex;
  // justify-content: center;
}

.section-4 .new-and-community-title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: 400;
  letter-spacing: 0;

  @media (min-width: 768px) {
    text-align: left;
    margin-bottom: 8px;
  }
}

.subscribition-form {
  margin-bottom: 30px;

  @media (min-width: 600px) {
    display: flex;
    justify-content: center;
  }

  @media (min-width: 768px) {
     display: block;
  }

  @media (min-width: 992px) {
    display: flex;
  }
}

.input-wrapper {
  @media (min-width: 465px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @media (min-width: 992px) {
    width: 405px;
  }

  @media (min-width: 768px) {
    justify-content: flex-start;
  }
}

.email-label {
  display: block;
  text-align: center;
  margin-bottom: 20px;
  font-weight: 300;

  @media (min-width: 600px) {
    width: 100%;
    text-align: center;
    margin-bottom: 16px;
  }

  @media (min-width: 768px) {
    text-align: left;
  }

  @media (min-width: 1440px) {
    margin-bottom: 14px;
  }
}

.input-email {
  width: 100%;
  height: 50px;
  padding: 10px 20px;
  margin-bottom: 20px;
  border: 1px solid #cccccc;
  background-color: #ffffff;
  border-radius: 50px;

  @media (min-width: 465px) {
    width: 70%;
  }

  @media (min-width: 600px) {
    width: 90%;
  }

  @media (min-width: 768px) {
    margin-bottom: 0;
  }

  @media (min-width: 1440px) {
    width: 363px;
  }
}

.submit-wrapper {
  @media (min-width: 465px) {
    display: flex;
    justify-content: center;
  }

  @media (min-width: 600px) {
    display: block;
    height: auto;
    padding-top: 46px;
  }

  @media (min-width: 768px) {
    margin-top: 28px;
    padding-top: 0;
  }

  @media (min-width: 992px) {
    margin-top: 45px;
  }
}

.btn-submit {
  width: 100%;
  height: 50px;

  @media (min-width: 465px) {
    width: 122px;
  }

  @media (min-width: 576px) {
    margin-right: 0;
  }

  @media (min-width: 768px) {
    height: 50px;
    margin: 0;
  }
}

.section-4 .follow-us-title {
  text-align: center;
  margin-bottom: 20px;
  font-size: 22px;
  font-weight: 400;
  letter-spacing: 0;

  @media (min-width: 768px) {
    text-align: left;
    margin-bottom: 38px;
  }

  @media (min-width: 1200px) {
    text-align: left;
  }
}

.follow-us-icons {
  display: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 350px;
  text-align: auto;

  @media (max-width: 767px) {
    max-width: none;
  }

  @media (min-width: 1200px) {
    justify-content: space-between;
  }
}

.follow-us-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 5px 10px;
  transition: all 0.3s ease;

  &:first-child {
    margin-left: 0;
  }

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    transition: all 0.3s ease;
    transform: translateY(-5px);

    img {
      // filter: grayscale(100%) contrast(50%);
      transition: all 0.3s ease;
    }
  }

  img {
    transition: all 0.3s ease;
  }
}