.section-use-cases {
  position: relative;

  .tilted-gray-shape {
    height: 700px;
    width: 100vw;
    // border: 1px solid gray;
    background-color: $section-color;
    z-index: 0;
    position: absolute;
    top: -300px;
    transform: skew(0, -12.5deg);
  }

  .wrapper {
    @media (min-width: 1440px) {
      padding-top: 65px;
      padding-bottom: 92px;
    }
  }

  .section-title {
    position: relative;
    z-index: 1;
    margin-bottom: 20px;

    @media (min-width: 768px) {
      margin-bottom: 56px;
    }
  }

  .use-case-card {
    // min-height: 332px;
    border-radius: 6px;
    box-shadow: 0 2px 4px 0px;
    margin-bottom: 34px;
    color: white;
    padding: 36px;
    position: relative;
    overflow: hidden;

    &--wording {
      max-width: 297px;
    }

    &--title {
      font-size: 32px;
      font-weight: 400;
      margin-bottom: 22px;
    }

    &--subtitle {
      font-size: 22px;
      opacity: 90%;
      line-height: 28px;
    }

    &--list {
      font-size: 22px;
      opacity: 90%;
      line-height: 28px;
      margin-top: 25px;
      margin-bottom: 35px;
    }

    &--image-container {
      position: absolute;
      top: 35px;
      right: 0px;
      z-index: 0;
    }

    &--button {
      display: inline;
      background-color: white;
      border-radius: 17px;
      padding: 7px 18px;
      font-size: 16px;
      font-weight: 400;
      position: relative;
      padding-left: 36px;
      transition: all 0.5s ease;

      &:hover {
        text-decoration: none;
        // box-shadow: 0 0 3px #eee;
        // opacity: 0.5;
        filter: brightness(1.2);
      }

      &::before {
        position: absolute;
        content: "";
        // top: 30%;
        top: 12px;
        left: 19px;
        width: 8px;
        height: 12px;
        background-image: url('../img/icons/blue-arrow.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }

    &.pink {
      background-color: $pink-card-color;
      .button {
        color: $pink-card-color;
        &::before {
          background-image: url('../img/icons/pink-arrow.svg');
        }
      }
    }

    &.violet {
      background-color: $violet-card-color;
      .button {
        color: $violet-card-color;
        &::before {
          background-image: url('../img/icons/violet-arrow.svg');
        }
      }
    }

    &.blue {
      background-color: $blue-card-color;
      .button {
        color: $blue-card-color;
        &::before {
          background-image: url('../img/icons/blue-arrow.svg');
        }
      }
    }

    &.orange {
      background-color: $orange-card-color;
      .button {
        color: $orange-card-color;
        &::before {
          background-image: url('../img/icons/orange-arrow.svg');
        }
      }
    }

    &.right-side-text {
      min-height: 335px;

      .use-case-card--image-container {
        position: absolute;
        top: 35px;
        left: -10px;
        z-index: 0;
        // border: 1px solid black;
      }

      .use-case-card--description {
        max-width: 300px;
        position: absolute;
        right: 15px;
        z-index: 1;

        @media (max-width: 380px) {
          & {
            position: relative;
            min-width: none;
          }
        }
      }
    }

    @media (min-width: 992px) and (max-width: 1350px) {
      .use-case-card--image-container img {
        opacity: 0.14;
      }
    }

    @media (max-width: 600px) {
      // & {
      //   min-height: 600px;
      // }
      .use-case-card--image-container img {
        opacity: 0.14;
      }

      .use-case-card--description {
        position: relative;
        z-index: 1;
        opacity: 1 !important;
      }
    }
  }

}