.switcher {
  margin-bottom: 45px;
  // border: 1px solid black;

  @media (max-width: 767px) {
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 25px;
  }

  .nav-tabs {
    position: relative;
    height: 48px;
    border: none;


    @media (min-width: 576px) {
      max-width: 600px;
      margin: 0 auto;
    }

    @media (min-width: 768px) {
      max-width: 667px;
    }

    @media (min-width: 1200px) {
      max-width: 780px;
    }
  }

  .nav-tabs .nav-item {
    position: absolute;
    height: 48px;

    &:first-child {
      width: 54%;
      top: 0;
      left: 0;
    }

    &:last-child {
      width: 54%;
      top: 0;
      right: 0;
    }
  }

  .nav-link {
    padding: 8px 10px 10px 10px;
    text-align: center;
    font-weight: 500;
    font-size: 23px;
    letter-spacing: 0.532764px;
    color: rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    background-color: $secondary-button;
    border: 1px solid #dfdfdf;
    box-shadow: 0 5px 7px -4px rgba(0, 0, 0, 0.2);

    @media (max-width: 1280px) {
      font-size: 20px;
    }

    @media (max-width: 600px) {
      font-size: 16px;
      padding: 0px 2px 2px 2px;
      // border-radius: 18px 10px 10px 18px;
    }
  }

  .nav-tabs .nav-item.show .nav-link,
  .nav-tabs .nav-link.active {
    background-color: $button-color;
    color: #fff;
    z-index: 2;
    position: relative;
    border-color: $button-color;

    &.violet {
      border-color: $violet-color;
      background-color: $violet-color;
    }
  }
}

.tab-content {
  margin: auto;
  padding: 0;
  // border: 1px solid black;

  @media (min-width: 992px) {
    max-width: 1000px;
  }

  transition: all ease 1s;

  img {
    width: 100%;
  }

  .tab-pane {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    padding: 0;
  }
}


