.footer-section {
  .wrapper {
    @media (min-width: 1440px) {
      padding-top: 63px;
      padding-bottom: 58px;
    }
  }
}

.blazar-copyright {
  color: #1998a2;
  font-size: 18px;
  font-weight: 500;
}

.footer-texts {
  margin-left: 15px;
  @media (max-width: 1024px) {
    margin: auto;
  }
}

.email-link {
  padding-left: 98px;
  color: $main-text-color;
  transition: all 0.2s;

  &:hover {
    color: #1998a2;
    text-decoration: none;
  }

  @media (max-width: 480px) {
    padding-left: 20px;
  }
}

