  //   }
  // }

  .wrapper {
    @media (min-width: 1440px) {
      padding-top: 57px;
      padding-bottom: 35px;
    }
  }

  .section-title {
    margin-bottom: 56px;
  }

  .technical-integrations-row {
    @media (min-width: 1200px) {
      margin-left: -44px;
      margin-right: -44px;
    }
  }

  .technical-integrations-col {
    margin-bottom: 61px;
    display: flex;
    justify-content: center;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: 576px) {
      &:last-child, &:nth-last-child(2) {
        margin-bottom: 0;
      }
    }

    @media (min-width: 992px) {
      margin-bottom: 61px !important;
    }

    @media (min-width: 1200px) {
      padding-left: 44px;
      padding-right: 44px;
    }
  }

  .technical-integrations-card {
    width: 80%;

    @media (min-width: 576px) {
      width: 90%;
    }

    @media (min-width: 768px) {
      width: 100%;
    }
  }

  .card-img-wrapper {
    margin-left: 0;
    display: flex;
    justify-content: center;
    margin-bottom: 0;

    @media (min-width: 576px) {
      justify-content: flex-start;
    }
  }

  .card-body {
    padding: 20px 0 20px 5px;
  }

  .card-title {
    font-size: 24px;
    letter-spacing: 0;
    font-weight: 400;
    color: #9354be;
    margin-bottom: 19px;
  }

  .card-text {
    font-size: 18px;
    font-weight: 300;
    line-height: 26px;
    color: #6b7c93;
    margin-bottom: 16px;
    letter-spacing: 0;
    max-width: 344px;

    @media (min-width: 768px) {
      font-size: 22px;
    }
  }

  .technical-integrations-link {
    position: relative;
    display: block;
    color: $secondary-color;
    font-weight: 400;
    letter-spacing: 0.59px;
    transition: all 0.3s ease;
    font-size: 18px;
    line-height: 22px;

    @media (min-width: 576px) {
      text-align: left;
    }

    @media (min-width: 768px) {
      font-size: 20px;
      line-height: 26px;
    }

    &:hover {
      color: $secondary-color-hover;
      margin-left: 5px;
      transition: all 0.3s ease;

      .technical-integrations-link__icon {
        background-image: url('../img/icons/link-arrow-darken.svg');
      }
    }
  }

  .technical-integrations-link__icon {
    position: relative;
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-left: 5px;
    background-image: url('../img/icons/link-arrow.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    transition: all 0.3s ease;
  }
}