.section-1 {
  position: relative;
  width: 100%;
  background-image: linear-gradient(20.16deg, #0A76E8 37.6%, #8A47DB 81.42%, #EA4C89 99.9%);
  z-index: 3;
  overflow: hidden;

  @media (min-width: 768px) {
    min-height: 700px;
  }

  @media (min-width: 768px) {
    min-height: 685px;
  }

  @media (min-width: 1900px) {
    min-height: 810px;
  }

  &::before {
    @media (min-width: 768px) {
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // background-image: url('../img/section-1/devices.png');
      background-image: url('../img/section-1/devices.png');
      background-repeat: no-repeat;
      // background-size: 60vw;
      // background-size: 70vw;
      // background-position: top calc(50% - 20px) right calc(50% - 240px);
      // background-position: top -30px right -200px;
      z-index: 1;
    }

    @media (min-width: 768px) and (max-width: 1023px) {
      background-size: 600px;
      background-position: top 150px right -170px;
      // background-position: top 200px right 0px;
      // background-size: 50vw;
    }

    @media (min-width: 1024px) {
      background-size: 70vw;
      background-position: top 30px right -200px;
    }

    @media (min-width: 1200px) {
      background-size: 60vw;
      background-position: top -30px right -200px;
      // background-position: top 30px right -200px;
    }

    @media (min-width: 1440px) {
      background-size: 50vw;
      background-position: top 0px right 0px;
    }

    @media (min-width: 1921px) {
      background-size: 40vw;
      background-position: top -100px right 200px;
    }

    // @media (min-width: 992px) {
    //   background-size: 660px;
    //   background-position: top 0 right calc(50% - 300px);
    // }

    // @media (min-width: 1200px) {
    //   background-size: 735px;
    //   background-position: top -30px right calc(50% - 370px);
    // }

    // @media (min-width: 1440px) {
    //   background-size: 850px;
    //   background-position: top -25px right calc(50% - 460px);
    // }

    // @media (min-width: 1900px) {
    //   background-size: 1070px;
    //   background-position: top -14px right calc(50% - 536px);
    // }
  }

  &::after {
    position: absolute;
    content: "";
    bottom: 0px;
    left: 0;
    width: 100%;
    height: 255px;
    background-image: linear-gradient(167.5deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 0.3) 50%, white 50.1%, white 100%);
    z-index: -1;

    @media (min-width: 768px) {
      height: 218px;
    }

    @media (min-width: 992px) {
      height: 265px;
    }
    
    @media (min-width: 1200px) {
      height: 320px;
    }

    @media (min-width: 1440px) {
      height: 353px;
    }

    @media (min-width: 1600px) {
      height: 425px;
    }

    @media (min-width: 1900px) {
      height: 100%;
      background-image: linear-gradient(167.5deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 65.4%, rgba(255, 255, 255, 0.3) 65.5%, white 65.6%, white 100%);
    }

    @media (min-width: 2560px) {
      background-image: linear-gradient(167.5deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 56.7%, rgba(255, 255, 255, 0.3) 56.7%, white 56.8%, white 100%);
    }
  }

  .wrapper {
    position: relative;
    z-index: 5;
    padding-top: 0;
    padding-bottom: 0;

    @media (min-width: 992px) {
      padding-top: 35px;
    }
  }
}

.hamburger {
  display: block;
  position: relative;
  width: 25px;
  height: 30px;
  cursor: pointer;

  &:before {
      content: "";
      position: absolute;
      top: 3px;
      left: 0;
      width: 100%;
      height: 5px;
      background-color: #ffffff;
      box-shadow: 0 10px 0 0 #ffffff,
                  0 20px 0 0 #ffffff;
  }
}

//NAVBAR//

.navbar {
  padding-left: 0;
  padding-right: 0;
  margin-bottom: 13px;

  @media (min-width: 768px) {
    padding-top: 11px;
    padding-bottom: 11px;
    margin-bottom: 26px;
  }

  @media (min-width: 992px) {
    padding-top: 0;
    margin-bottom: 52.5px;
  }

  @media (min-width: 1200px) {
    margin-bottom: 30px;
  }

  @media (min-width: 1440px) {
    margin-bottom: 60px;
  }

  @media (min-width: 1900px) {
    margin-bottom: 116px;
  }
}

.navbar-dark .navbar-toggler {
  border: none;
}

.navbar-brand {
  @media (min-width: 992px) {
    margin-right: 75px;
  }

  // @media (min-width: 1200px) {
  //   margin-right: 190px;
  // }
}

.navbar-logo {
  transition: all 0.2s;

  &:hover {
    //opacity: 0.5;
    //transition: all 0.2s;
  }
}

.navbar-nav {
  flex-wrap: wrap;
}

.navbar-expand-lg .navbar-nav {
  & .nav-item:first-child .nav-link {
    margin-left: 0;
  }

  & .nav-link {
    letter-spacing: 0.45px;

    @media (min-width: 992px) {
      padding: 0;
      margin: 0 10.75px;
    }

    @media (min-width: 1200px) {
      margin: 0 23px;
    }
  }
}

.navbar-dark .navbar-nav .nav-link {
  color: $header-color;
  font-size: 22px;
  // font-weight: 400;
  position: relative;
  transition: all 0.2s;
  letter-spacing: 0.45px;

  @media (max-width: 991px) {
    display: inline-block;
  }

  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    background-color: $header-color-semi;
    bottom: -4px;
    left: 0;
    transform: scale(0);
    opacity: 0;
    transition: all 0.5s ease;
  }
}

.navbar-dark .navbar-nav .active > .nav-link {
  transition: all 0.2s;

  &:hover {
    color: $header-color-semi;
  }

  &::before {
    background-color: $header-color;
  }

  &:hover::before {
    background-color: $header-color-semi;
    transition: none;
  }
}

.navbar-dark .navbar-nav .nav-link:hover {
  transition: all 0.2s;

  &::before {
    transform: scale(1);
    opacity: 1;
    transition: all 0.2s ease;
  }
}

.nav-item.active .nav-link {
  &::before {
    opacity: 1;
    background-color: $header-color;
  }
}

.navbar-right {
  margin-right: 27px;
}

.navbar-right .nav-item {
  position: relative;

  @media (min-width: 992px) {
    &::after {
      position: absolute;
      content: "";
      top: 0;
      right: 0;
      height: 100%;
      width: 1px;
      background-color: $header-color;
    }

    &:last-child::after {
      content: none;
    }
  }
}

//OVERVIEW//

.overview {
  color: $header-color;
  letter-spacing: 0.64px;
  padding-bottom: 30px;
  min-height: 400px;

  @media (min-width: 768px) {
    padding-bottom: 0;
    margin-top: 88px;
  }
}

.overview-title {
  font-size: 35px;
  line-height: 42px;
  letter-spacing: 0.64px;
  margin-bottom: 21px;

  @media (min-width: 768px) {
     max-width: 400px;
  }

  @media (min-width: 992px) {
    max-width: 530px;
  }

  @media (min-width: 1200px) {
    max-width: 620px;
    margin-bottom: 54px;
    font-size: 42px;
    line-height: 52px;
  }

  @media (min-width: 1440px) {
    min-width: 100%;
    // margin-bottom: 49px;
  }
}

.overview-tagline {
  letter-spacing: 0.94px;
  margin-bottom: 25px;

  @media (min-width: 768px) {
    padding-right: 30px;
    margin-bottom: 0;
    max-width: 455px;
    // min-height: 210px;
  }

  @media (min-width: 992px) {
    max-width: 100%;
  }

  @media (min-width: 1200px) {
    min-height: 0;
    max-width: 620px;
  }
}

.overview-buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 0px;

  @media (min-width: 768px) {
    margin-top: 30px;
    margin-bottom: 35px;
    justify-content: flex-start;
  }

  @media (min-width: 992px) {
    margin-top: 84px;
    margin-bottom: 0;
  }

  // @media (min-width: 1440px) {
  //   margin-top: 46px;
  // }
}

.overview-button {
  min-height: 50px;
  font-size: 21px;
  font-weight: 400;
  letter-spacing: 0.52px;
  text-transform: uppercase;
  box-shadow: 0 2px 4px rgba(100, 100, 100, 0.5);
  border-radius: 4px;
  border: none;

  @media (min-width: 1200px) {
    font-size: 21px;
    line-height: 33px;
  }
}

.main-btn {
  position: relative;
  color: #fff;
  padding: 8px 24px 9px 44px;
  background-color: $button-color;
  border-radius: 50px;

  &:hover {
    background-color: $button-color-hover;
    color: #fff;
  }

  &:active {
    background-color: $button-color-active;
  }

  &::before {
    position: absolute;
    content: "";
    top: 30%;
    left: 19px;
    width: 11px;
    height: 18px;
    background-image: url('../img/icons/arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
}

.btn-success:not(:disabled):not(.disabled):active,
.btn-success:not(:disabled):not(.disabled).active,
.show > .btn-success.dropdown-toggle {
  color: #fff;
  background-color: $button-color-active;
  border-color: $button-color-active;
}

.btn-success:not(:disabled):not(.disabled).active:focus,
.btn-success:not(:disabled):not(.disabled):active:focus,
.show>.btn-success.dropdown-toggle:focus {
  box-shadow: 0 0 0 0;
}

.btn-success.focus, .btn-success:focus {
  box-shadow: 0 0 0 0;
}

.btn-light {
  background-color: $header-color;
  color: $secondary-color;

  &:hover {
    //color: $secondary-color-lighten;
  }
}

.overview-image-wrapper {
  display: flex;
  margin: 0 auto;
  margin-top: 55px;

  @media (min-width: 575px) {
    display: flex;
    //margin-bottom: 35px;
    padding: 0 10px;
  }
  
  @media (min-width: 768px) {
    flex-direction: column;
    justify-content: center;
    margin-bottom: 0;
  }

  @media (min-width: 992px) {
    display: flex;
    margin-top: auto;
    margin-left: auto;
    margin-bottom: 30px;
  }

  @media (min-width: 1200px) {
    margin-top: auto;
    margin-left: 48px;
    width: 520px;
    height: 357px;
    padding: 0;
  }
}

.overview-image {
